@import "./../../libs/variables.scss";

.main{
    display: flex;
    width: 100%;
    height: 100vh;
    justify-content: center;
    
    .left, .right {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
    }

    .left {
        background-color: lightblue;
    }

    @media all and (max-width: 990px) {
        .left {
            display: none;
        }
      }
    
}